.fixed-elements {
    position: fixed;
    width: 100%; }

.logo {
    width: 6vw;
    padding: 15px; }

.static-line {
    border-top: 3px solid;
    border-color: $dark;
    width: 96vw;
    margin: 20px;
    margin-top: 4vh;
    position: fixed;
    z-index: 1; }

.circle {
    width: 2vw;
    position: fixed;
    bottom: 2vw;
    right: 2vw;
    z-index: -1; }

.box-element {
    height: 100vh;
    width: 20vw; }


.titles-wrap {
    position: relative;
    margin-top: 6vh; }

.large-title {
    padding-left: 20px;
    opacity: 0;
    position: absolute; }

.large-title-italic {
    position: fixed;
    right: 2vw;
    bottom: 1vw;
    text-align: right;
    z-index: 1;
    color: $light; }

.is--active {
    opacity: 1; }

.light {
    color: $light; }

.greens {
    width: 50%;
    float: right; }

.garden-images-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 100%; }

.garden-image {
    width: 20vw;
    margin-bottom: 5vh; }

.garden-image:nth-child(even) {
    justify-self: end;
    margin-top: 5vh; }

.event-flex {
    display: flex;
    justify-content: space-between;
    align-items: center; }

.event-flex p {
    width: 30%; }

.events-container {
    background-color: $light; }

.book-btn {
    height: 20px;
    width: 20px;
    background-color: $dark;
    border-radius: 50%; }

.book-btn-active {
    background-color: $dark;
    color: $light;
    border-radius: 15px;
    font-size: 0.8em;
    padding: 5px 10px;
    display: none; }
